package data

import domain.PrestigeRemoteDataSource
import domain.author.AuthorsResponse
import domain.excerpt.ExcerptsResponse
import domain.quote.Quote
import domain.quote.QuotesResponse
import domain.quote.RandomQuoteResponse
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.get

class PrestigeRemoteDataSourceImpl(
    private val baseUrl: String,
    private val client: HttpClient,
) : PrestigeRemoteDataSource {
    override suspend fun getExcerpts(): ExcerptsResponse {
        val response = client.get("$baseUrl/excerpts")
        return response.body<ExcerptsResponse>()
    }

    override suspend fun getAuthorsById(
        excerptId: String,
        shouldRefresh: Boolean,
    ): AuthorsResponse {
        val endPoint = "$baseUrl/authors/$excerptId${if (shouldRefresh) "/refresh" else ""}"
        val response = client.get(endPoint)
        return response.body<AuthorsResponse>()
    }

    override suspend fun getQuotesByAuthorId(
        authorId: String,
        shouldRefresh: Boolean,
    ): QuotesResponse {
        val endPoint = "$baseUrl/quotes/$authorId${if (shouldRefresh) "/refresh" else ""}"
        val response = client.get(endPoint)
        return response.body<QuotesResponse>()
    }

    override suspend fun getRandomQuoteByAuthorId(authorId: String): Quote {
        val endPoint = "$baseUrl/quotes/$authorId/random"
        val response = client.get(endPoint)
        return response.body<Quote>()
    }

    override suspend fun getRandomQuote(allowOnlyAcceptable: Boolean): Quote {
        val endPoint = if (allowOnlyAcceptable) {
            "$baseUrl/quotes/random?isAcceptable=$allowOnlyAcceptable"
        } else {
            "$baseUrl/quotes/random"
        }
        val response = client.get(endPoint)
        return response.body<RandomQuoteResponse>().randomQuote
    }
}