package data

import domain.PrestigeLocalDataSource
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.update

class PrestigeInMemoryDataSource : PrestigeLocalDataSource {
    override val isAcceptableMode = MutableStateFlow(true)
    override suspend fun toggleIsAcceptableMode() {
        isAcceptableMode.update { !isAcceptableMode.value }
    }
}