import androidx.compose.material3.ExperimentalMaterial3Api
import androidx.compose.runtime.Composable
import androidx.compose.runtime.SideEffect
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import com.arkivanov.decompose.extensions.compose.stack.Children
import com.arkivanov.decompose.extensions.compose.stack.animation.fade
import com.arkivanov.decompose.extensions.compose.stack.animation.stackAnimation
import com.arkivanov.decompose.extensions.compose.subscribeAsState
import navigation.RootComponent
import ui.AppTheme
import ui.authors.AuthorsScreen
import ui.excerpts.ExcerptsScreen
import ui.favorites.FavoritesScreen
import ui.home.HomeScreen
import ui.menu.Options
import ui.quotes.QuotesScreen

data class ScreenBottom(val title: String, val onClick: () -> Unit)

@Composable
internal fun App(
    root: RootComponent,
) {
    var isDarkTheme by remember { mutableStateOf(false) }
    var bottomBarVisible by remember { mutableStateOf(true) }
    AppTheme(isDarkTheme = isDarkTheme) {
        val childStack by root.childStack.subscribeAsState()
        val screens by remember {
            mutableStateOf(
                listOf(
                    ScreenBottom(title = "Home", onClick = root::openHome),
                    ScreenBottom(title = "Excerpts", onClick = root::openExcerpts),
                    ScreenBottom(title = "Favorites", onClick = root::openFavorites),
                )
            )
        }
        Options(
            isVisible = bottomBarVisible,
            options = screens,
            onChangeThemeClick = { isDarkTheme = !isDarkTheme },
            screenContent = {
                SideEffect {
                    bottomBarVisible = when (childStack.items.last().instance) {
                        is RootComponent.Child.Excerpts,
                        is RootComponent.Child.Home,
                        is RootComponent.Child.Favorites,
                        -> true

                        else -> false
                    }
                }
                Children(
                    stack = childStack,
                    animation = stackAnimation(fade()),
                ) { child ->
                    when (val instance = child.instance) {
                        is RootComponent.Child.Excerpts -> {
                            ExcerptsScreen(
                                component = instance.component,
                            )
                        }

                        is RootComponent.Child.Authors -> {
                            AuthorsScreen(
                                component = instance.component,
                            )
                        }

                        is RootComponent.Child.Quotes -> {
                            QuotesScreen(
                                component = instance.component,
                            )
                        }

                        is RootComponent.Child.Home -> {
                            HomeScreen(
                                component = instance.component
                            )
                        }

                        is RootComponent.Child.Favorites -> {
                            FavoritesScreen(
                                component = instance.component
                            )
                        }
                    }
                }
            }
        )
    }
}

