import kotlin.math.sin
import kotlin.math.sqrt

@Composable
fun ExcerptsScreen(
    modifier: Modifier = Modifier,
    component: ExcerptsComponent,
) {
    val state by component.state.collectAsState()
    val infiniteTransition = rememberInfiniteTransition()

    val angle by infiniteTransition.animateFloat(
        initialValue = 0f,
        targetValue = 360f,
        animationSpec = infiniteRepeatable(
            animation = tween(8000, easing = LinearEasing),
            repeatMode = RepeatMode.Reverse
        )
    )

    Box(
        modifier = modifier.fillMaxSize(),
        contentAlignment = Alignment.Center
    ) {
        LazyRow {
            items(state.excerpts) {
                Row {
                    Box(
                        modifier = Modifier
                            .clip(RoundedCornerShape(50))
                            .clickable {
                                component.onExcerptClicked(
                                    it.notionId
                                )
                            }
                            .gradientBackground(
                                colors = listOf(
                                    MaterialTheme.colorScheme.background,
                                    MaterialTheme.colorScheme.secondary,
                                ),
                                angle = angle,
                            )
                            .padding(16.dp),
                        contentAlignment = Alignment.Center,
                    ) {
                        Text(
                            text = it.excerptName,
                        )
                    }
                    AnimatedVisibility(it.itemsInside != null) {
                        Text(text = it.itemsInside.toString())
                    }
                }
                Spacer(modifier = Modifier.width(6.dp))
            }
        }
    }
}

fun Modifier.gradientBackground(colors: List<Color>, angle: Float) = this.then(
    Modifier.drawBehind {
        val angleRad = angle / 180f * PI
        val x = cos(angleRad).toFloat() //Fractional x
        val y = sin(angleRad).toFloat() //Fractional y

        val radius = sqrt(size.width.pow(2) + size.height.pow(2)) / 2f
        val offset = center + Offset(x * radius, y * radius)

        val exactOffset = Offset(
            x = min(offset.x.coerceAtLeast(0f), size.width),
            y = size.height - min(offset.y.coerceAtLeast(0f), size.height)
        )

        drawRect(
            brush = Brush.linearGradient(
                colors = colors,
                start = Offset(size.width, size.height) - exactOffset,
                end = exactOffset
            ),
            size = size
        )
    }
)


fun Modifier.radialGradientBackground(
    colors: List<Color>,
    animatedX: Float,
    animatedY: Float,
) = this.then(
    Modifier.drawBehind {
        val side1 = size.width
        val side2 = size.height

        drawRect(
            brush = Brush.radialGradient(
                0.0f to colors[0],
                0.3f to colors[1],
                1.0f to colors[2],
                center = Offset(side1 / animatedX, side2 / animatedY),
                radius = ((side1 * 2f) + (side2 * 2f)) / 2f,
//                tileMode = TileMode.Repeated,
            )
        )
    }
)
