@Composable
expect fun Menu(
    modifier: Modifier = Modifier,
)

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun Options(
    isVisible: Boolean,
    options: List<ScreenBottom>,
    onChangeThemeClick: () -> Unit,
    screenContent: @Composable () -> Unit,
) {
    var selectedItem by remember { mutableIntStateOf(0) }
    val infiniteTransition = rememberInfiniteTransition()
    val animatedX by infiniteTransition.animateFloat(
        initialValue = 1.4f,
        targetValue = 3.0f,
        animationSpec = infiniteRepeatable(
            animation = tween(durationMillis = 6000),
            repeatMode = RepeatMode.Reverse
        )
    )
    val animatedY by infiniteTransition.animateFloat(
        initialValue = 2.2f,
        targetValue = 1.8f,
        animationSpec = infiniteRepeatable(
            animation = tween(durationMillis = 3000),
            repeatMode = RepeatMode.Reverse
        )
    )
    Scaffold(
        topBar = {
            if (isVisible) {
                TopAppBar(
                    title = {
                        Menu()
                    },
                    actions = {
                        Button(onClick = onChangeThemeClick) {
                            Text(text = "Change Theme")
                        }
                    }
                )
            }
        },
        bottomBar = {
            if (isVisible) {
                BottomAppBar(
                    modifier = Modifier.fillMaxWidth(),
                    actions = {
                        options.forEachIndexed { index, screen ->
                            NavigationBarItem(
                                icon = {},
                                label = {
                                    Text(
                                        text = screen.title,
                                        style = MaterialTheme.typography.labelLarge,
                                        fontWeight = FontWeight.Light
                                    )
                                },
                                selected = selectedItem == index,
                                onClick = {
                                    selectedItem = index
                                    screen.onClick()
                                },
                                colors = NavigationBarItemDefaults.colors(
                                    selectedIconColor = MaterialTheme.colorScheme.primary
                                )
                            )
                        }
                    }
                )
            }
        },
        content = { paddingValues ->
            Box(
                modifier = Modifier.fillMaxSize()
                    .padding(paddingValues)
                    .radialGradientBackground(
                        colors = listOf(
                            MaterialTheme.colorScheme.secondaryContainer,
                            MaterialTheme.colorScheme.background,
                            MaterialTheme.colorScheme.background,
                        ),
                        animatedX = animatedX,
                        animatedY = animatedY,
                    )
            ) {
                screenContent.invoke()
            }
//            SideEffect {
//                bottomBarVisible = when (childStack.items.last().instance) {
//                    is RootComponent.Child.Excerpts,
//                    is RootComponent.Child.Home,
//                    is RootComponent.Child.Favorites,
//                    -> true
//
//                    else -> false
//                }
//            }
        }
    )
}