package navigation

import com.arkivanov.decompose.ComponentContext
import com.arkivanov.decompose.ExperimentalDecomposeApi
import com.arkivanov.decompose.router.stack.StackNavigation
import com.arkivanov.decompose.router.stack.bringToFront
import com.arkivanov.decompose.router.stack.childStack
import com.arkivanov.decompose.router.stack.pop
import com.arkivanov.decompose.router.stack.pushNew
import com.arkivanov.decompose.router.stack.webhistory.WebHistoryController
import com.arkivanov.essenty.lifecycle.Lifecycle
import com.arkivanov.essenty.lifecycle.LifecycleOwner
import com.arkivanov.essenty.lifecycle.doOnDestroy
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.cancel
import kotlinx.serialization.Serializable
import presentation.authors.AuthorsComponent
import presentation.excerpts.ExcerptsComponent
import presentation.favorites.FavoritesComponent
import presentation.home.HomeComponent
import presentation.quotes.QuotesComponent
import kotlin.coroutines.CoroutineContext

@OptIn(ExperimentalDecomposeApi::class)
class RootComponent(
    componentContext: ComponentContext,
) : ComponentContext by componentContext {

    private val navigation = StackNavigation<Configuration>()
    val childStack = childStack(
        source = navigation,
        serializer = Configuration.serializer(),
        initialConfiguration = Configuration.Home,
        handleBackButton = true,
        childFactory = ::createChild,
    )

    fun openHome() {
        navigation.bringToFront(Configuration.Home)
    }

    fun openExcerpts() {
        navigation.bringToFront(Configuration.Excerpts)
    }

    fun openFavorites() {
        navigation.bringToFront(Configuration.Favorites)
    }

    @OptIn(ExperimentalDecomposeApi::class)
    private fun createChild(
        config: Configuration,
        context: ComponentContext,
    ): Child {
        val mainContext = Dispatchers.Main
        return when (config) {
            Configuration.Excerpts -> Child.Excerpts(
                component = ExcerptsComponent(
                    mainContext = mainContext,
                    componentContext = context,
                    onExcerptClicked = { id ->
                        navigation.pushNew(Configuration.Authors(excerptId = id))
                    },
                ),
            )

            is Configuration.Authors -> Child.Authors(
                component = AuthorsComponent(
                    mainContext = mainContext,
                    excerptId = config.excerptId,
                    componentContext = context,
                    onAuthorClicked = { id ->
                        navigation.pushNew(Configuration.Quotes(authorId = id))
                    },
                    onBack = { navigation.pop() },
                ),
            )

            is Configuration.Quotes -> Child.Quotes(
                component = QuotesComponent(
                    mainContext = mainContext,
                    authorId = config.authorId,
                    componentContext = context,
                    onBack = { navigation.pop() },
                ),
            )

            Configuration.Home -> Child.Home(
                component = HomeComponent(
                    mainContext = mainContext,
                    componentContext = context,
                )
            )

            Configuration.Favorites -> Child.Favorites(
                component = FavoritesComponent(
                    mainContext = mainContext,
                    componentContext = context,
                )
            )
        }
    }

    sealed class Child {
        data class Excerpts(val component: ExcerptsComponent) : Child()
        data class Authors(val component: AuthorsComponent) : Child()
        data class Quotes(val component: QuotesComponent) : Child()
        data class Home(val component: HomeComponent) : Child()
        data class Favorites(val component: FavoritesComponent) : Child()
    }

    @Serializable
    sealed interface Configuration {

        @Serializable
        data object Excerpts : Configuration

        @Serializable
        data class Authors(val excerptId: String) : Configuration

        @Serializable
        data class Quotes(val authorId: String) : Configuration

        @Serializable
        data object Home : Configuration

        @Serializable
        data object Favorites : Configuration
    }
}

fun CoroutineScope(context: CoroutineContext, lifecycle: Lifecycle): CoroutineScope {
    val scope = CoroutineScope(context)
    lifecycle.doOnDestroy(scope::cancel)
    return scope
}

fun LifecycleOwner.coroutineScope(context: CoroutineContext): CoroutineScope =
    CoroutineScope(context, lifecycle)
