package presentation.authors

import com.arkivanov.decompose.ComponentContext
import domain.PrestigeLocalDataSource
import domain.PrestigeRemoteDataSource
import domain.author.removePlusFromAuthorName
import kotlinx.coroutines.SupervisorJob
import kotlinx.coroutines.delay
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.SharingStarted
import kotlinx.coroutines.flow.combine
import kotlinx.coroutines.flow.stateIn
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch
import navigation.coroutineScope
import org.koin.core.component.KoinComponent
import org.koin.core.component.get
import kotlin.coroutines.CoroutineContext

class AuthorsComponent(
    componentContext: ComponentContext,
    mainContext: CoroutineContext,
    private val excerptId: String,
    private val onAuthorClicked: (String) -> Unit,
    private val onBack: () -> Unit,
) : ComponentContext by componentContext, KoinComponent {
    private val api: PrestigeRemoteDataSource = get()
    private val localDataSource: PrestigeLocalDataSource = get()
    private val coroutineScope = coroutineScope(mainContext + SupervisorJob())

    private val _state = MutableStateFlow(AuthorsState())
    val state = combine(_state, localDataSource.isAcceptableMode) { state, allowOnlyAcceptable ->
        val filteredAuthors = if (allowOnlyAcceptable) {
            state.authors.filter { it.isAcceptable }.map { it.removePlusFromAuthorName() }
        } else {
            state.authors.map { it.removePlusFromAuthorName() }
        }
        state.copy(authors = filteredAuthors)
    }.stateIn(coroutineScope, SharingStarted.WhileSubscribed(5000L), AuthorsState())

    init {
        coroutineScope.launch {
            _state.update { state ->
                val authors = api.getAuthorsById(excerptId = excerptId).authors
                delay(300)
                state.copy(isLoading = false, authors = authors)
            }
        }
    }

    fun onAuthorClicked(id: String) = onAuthorClicked.invoke(id)
    fun onRefresh() {
        coroutineScope.launch {
            _state.update {
                it.copy(isLoading = true)
            }
            delay(1000)
            val updatedAuthors =
                api.getAuthorsById(excerptId = excerptId, shouldRefresh = true).authors
            _state.update {
                it.copy(
                    authors = updatedAuthors,
                    isLoading = false
                )
            }
        }
    }

    fun onBack() = onBack.invoke()
}