package domain.author

import domain.quote.Quote
import kotlinx.serialization.Serializable

@Serializable
data class AuthorsResponse(val authors: List<Author>)

@Serializable
data class Author(
    val notionId: String,
    val authorName: String,
    val isAcceptable: Boolean,
    val itemsInside: Int? = null,
)

fun Author.removePlusFromAuthorName(): Author {
    return this.copy(authorName = this.authorName.replace("+", ""))
}