package domain

import domain.author.AuthorsResponse
import domain.excerpt.ExcerptsResponse
import domain.quote.Quote
import domain.quote.QuotesResponse

interface PrestigeRemoteDataSource {
    suspend fun getExcerpts(): ExcerptsResponse

    suspend fun getAuthorsById(
        excerptId: String,
        shouldRefresh: Boolean = false,
    ): AuthorsResponse

    suspend fun getQuotesByAuthorId(
        authorId: String,
        shouldRefresh: Boolean = false,
    ): QuotesResponse

    suspend fun getRandomQuoteByAuthorId(
        authorId: String,
    ): Quote

    suspend fun getRandomQuote(allowOnlyAcceptable: Boolean): Quote
}